import "./namedisplay.css";

export const nameDisplay = (selectedMathemes) => {
  let namedDiscourses = [
    ["S\u2082", "a", "$", "S\u2081"],
    ["S\u2081", "S\u2082", "a", "$"],
    ["$", "S\u2081", "S\u2082", "a"],
    ["a", "$", "S\u2081", "S\u2082"],
  ];
  let discourseNames = [
    "University Discourse",
    "Master's Discourse",
    "Hysteric's Discourse",
    "Analyst's Discourse",
  ];

  let isEqual = namedDiscourses.find(discourse => 
    discourse.every((value, index) => 
      value === selectedMathemes[index]));

  let discourse = discourseNames[namedDiscourses.indexOf(isEqual)] 
  return isEqual !== undefined
  ? discourse
  : "Unknown Discourse";
};
