import React, { useState } from "react";
import { DiscourseSelector } from "../discourseselector";
import { DiscourseDisplay } from "../discoursedisplay";

import { tavarish } from "../../../assets";
import { cwarrow } from "../../../assets";
import { linarrow } from "../../../assets";

import "./discoursegrid.css";

export const DiscourseGrid = () => {
  const [selectedMathemes, setSelectedMathemes] = useState([
    " ",
    " ",
    " ",
    " ",
  ]);

  const rotateDiscourse = (mathemearray, rotation) => {
    let array = [...mathemearray];
    for (let i = 0; i < rotation; i++) {
      let lastElement = array.pop();
      array.unshift(lastElement);
    }
    return array;
  };

  const customOperation = (discourse) => {
    // creates capitalist's ring from master's ring
    let array = [...discourse];
    let temp = array[3];
    array[3] = array[0];
    array[0] = temp;
    return array;
  };

  const handleClear = (event) => {
    setSelectedMathemes(Object.assign([], [" ", " ", " ", " "]));
  }

  let selectorProps = {
    selectedMathemes,
    setSelectedMathemes,
  };
  return (
    <div className="outer-grid">
      <button onClick={handleClear}>Clear</button>
      <div className="custom-operation">
        <DiscourseDisplay displayValues={customOperation(selectedMathemes)} />
      </div>
      <div className="custom-operation2">
        <DiscourseDisplay
          displayValues={rotateDiscourse(customOperation(selectedMathemes), 1)}
        />
      </div>
      <div className="custom-operation3">
        <DiscourseDisplay
          displayValues={rotateDiscourse(customOperation(selectedMathemes), 2)}
        />
      </div>
      <div className="custom-operation4">
        <DiscourseDisplay
          displayValues={rotateDiscourse(customOperation(selectedMathemes), 3)}
        />
      </div>
      <div className="image-container linarrow-image-container">
        <img className="linarrow1" src={linarrow} alt="arrow" />
      </div>

      <div className="discourse-grid">
        <DiscourseSelector {...selectorProps} />
        <DiscourseDisplay
          displayValues={rotateDiscourse(selectedMathemes, 1)}
        />
        <DiscourseDisplay
          displayValues={rotateDiscourse(selectedMathemes, 2)}
        />
        <DiscourseDisplay
          displayValues={rotateDiscourse(selectedMathemes, 3)}
        />
        <div className="image-container stalin-image-container">
          <img
            className="stalin"
            src={tavarish}
            alt="comrade stalin ripping it"
            title="comrade stalin ripping it"
            // width="200"
            // height="200"
          />
        </div>

        <div className="image-container cwarrow-image-container">
          <img className="cwarrow1" src={cwarrow} alt="clockwise arrow" />
        </div>
        <div className="image-container cwarrow-image-container">
          <img className="cwarrow2" src={cwarrow} alt="clockwise arrow" />
        </div>
        <div className="image-container cwarrow-image-container">
          <img className="cwarrow4" src={cwarrow} alt="clockwise arrow" />
        </div>
        <div className="image-container cwarrow-image-container">
          <img className="cwarrow3" src={cwarrow} alt="clockwise arrow" />
        </div>
      </div>
    </div>
  );
};
